.container {
    display: flex;
    flex-direction: row;
    margin: 5px auto;
    border-radius: 8px;
    width: 100%;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
    font-size: 20px;
    min-height: 35px;
    background-color: #FFFDF8;
    align-items: center;
    justify-content: space-between;
    min-height: 100px;
}

.container:hover {
    background-color: #d8d0d0;
    cursor: pointer;
}